@import "../components/variables";

@media screen and (max-width: 985px), screen and (max-device-width: 985px){
  body{
    .certificates{
      .container{
        .content__certificates{
          .firstBlock,
          .secondBlock{
            ul{
              li{
                width: 20%;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 750px), screen and (max-device-width: 750px){
  body{
    .certificates{
      .container{
        .content__certificates{
          .firstBlock,
          .secondBlock{
            ul{
              li{
                width: 25%;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 525px), screen and (max-device-width: 525px){
  body{
    .certificates{
      .container{
        .content__certificates{
          &__headline{
            a{
              width: 200px;
              height: 40px;
              line-height: 40px;
              i{
                line-height: 40px;
              }
            }
          }
          .firstBlock,
          .secondBlock{
            ul{
              li{
                width: 35%;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 525px), screen and (max-device-width: 525px){
  body{
    .certificates{
      .container{
        .content__certificates{
          &__headline{
            a{
              width: 50px;
              span{
                display: none;
              }
            }
          }
          .firstBlock,
          .secondBlock{
            ul{
              li{
                width: 65%;
              }
            }
          }
        }
      }
    }
  }
}